import vue from 'vue'
import VueRouter from 'vue-router'
import p2_1 from '../pages/p2-1.vue'
import p2_2 from '../pages/p2-2.vue'
vue.use(VueRouter)

const router = new VueRouter({
    routes:[
        // '/' 默认一个初始页面
        {path: '/', component:p2_1},
        {path: '/2-1', component: p2_1},
        {path: '/2-2', component: p2_2}
    ]
})

export default router